@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url("https://cdn.discordapp.com/attachments/798763577850462218/798768265333178368/background.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  display: none;
}

.App {
  width: 100%;
  font-family: "Poppins";
}

.main {
  margin-left: 6%;
  font-family: "Poppins";
  height: 100vh;
}

.main > img {
  margin-top: 10vh;
  height: 250px;
  width: 250px;
  margin-left: 1vh;
}

.main > h1 {
  color: white;
  font-size: 55px;
  margin-top: 20px;
}

.main > h1 > span {
  color: yellow;
}

.arrow1 {
  height: 30px !important;
  width: 30px !important;
  padding: 10px;
  background-color: white;
  border-radius: 50%;
  margin-left: 40vw !important;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .main {
    margin-left: 6%;
    font-family: "Poppins";
    height: 100vh;
    margin-bottom: 100%;
  }

  .main > img {
    margin-top: 10vh;
    height: 250px;
    width: 250px;
    margin-left: 1vh;
  }

  .main > h1 {
    color: white;
    font-size: 55px;
    margin-top: 20px;
  }

  .main > h1 > span {
    color: yellow;
  }

  .arrow1 {
    height: 30px !important;
    width: 30px !important;
    padding: 10px;
    background-color: white;
    border-radius: 50%;
    margin-left: 40vw !important;
    cursor: pointer;
  }
}

.videoPlayer {
  font-family: "Poppins";
  justify-content: center;
  align-items: center;
  display: block;
  height: 100vh;
}

.videoPlayer > h1 {
  color: white;
  font-size: 45px;
  text-align: center;
  padding: 20px;
}

.YTPlayer {
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
  height: 70vh;
}

.arrow2 {
  height: 30px !important;
  width: 30px !important;
  padding: 10px;
  background-color: white;
  border-radius: 50%;
  margin-left: 45vw !important;
  cursor: pointer;
  margin-top: 10px;
}

@media only screen and (max-width: 600px) {
  .videoPlayer {
    font-family: "Poppins";
    justify-content: center;
    align-items: center;
    display: block;
    height: 100vh;
    margin-bottom: 100%;
  }
}

.challenge {
  margin-top: 20vh;
  margin-left: 6%;
  max-width: 450px;
  height: 90vh;
}

.challenge > h1 {
  color: white;
  font-size: 50px;
  margin-top: 30vh !important;
}

.challenge > h2 {
  color: white;
  font-size: 20px;
  max-width: 450px;
  margin-top: 40px;
}

.challenge > h2 > span {
  color: yellow;
}

#linkInput {
  padding: 10px;
  font-family: "Poppins";
  border-radius: 5px;
  border: none;
  outline: none;
  margin-top: 30px;
  width: 430px;
  font-weight: 100;
  color: #2196f3;
}

#linkInput::-webkit-input-placeholder {
  color: #2196f3;
}

#linkInput:-ms-input-placeholder {
  color: #2196f3;
}

#linkInput::placeholder {
  color: #2196f3;
}

#btnSubmit {
  background: #2196f3;
  color: white;
  padding: 10px 30px;
  border: none;
  outline: none;
  font-family: "Poppins";
  border-radius: 5px;
  float: right;
  cursor: pointer;
  margin-top: 20px;
}

.arrow3 {
  height: 30px !important;
  width: 30px !important;
  padding: 10px;
  background-color: white;
  border-radius: 50%;
  margin-left: 40vw !important;
  cursor: pointer;
  margin-top: 60px !important;
}

@media only screen and (max-width: 600px) {
  #linkInput {
    padding: 10px;
    font-family: "Poppins";
    border-radius: 5px;
    border: none;
    outline: none;
    margin-top: 30px;
    width: 430px;
    font-weight: 100;
    color: #2196f3;
    margin-right: 10%;
  }
}

